.app-info {
  display: flex;

  img {
    width: 3rem;
    height: 3rem;
    margin-top: 50px;
    margin-right: 10px;
  }
  h2 {
    font-size: 45px;
    padding: 60px;
    width: 50%;
  }
  h4 {
    color: $clr1;
  }
  .info-card {
    display: flex;
    align-self: center;
  }
  .info-cards {
    display: flex;
    flex-direction: column;
  }
}
.info-wrapper{
  background-color: $clr4;

}