.app-stories {
  text-align: center;

  .person-img {
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    border: 0.5px solid $clr5;
  }
  .quotes-img {
    width: 33%;
    transform: translateY(53px);
    position: relative;
    z-index: -1;
  }
  .stories-cards {
    display: flex;
  }
  .stories-card {
    margin: 10px;
  }
  span {
    color: $clr5;
  }
  h1 {
    font-size: 48px;
  }
  h4 {
    color: $clr5;
  }
  p{
    z-index: 4;
  }
}
