.nav {
  display: flex;
  justify-content: space-between;
  color: white;
  margin-top: 20px;
  .logo {
    font-size: 30px;
    cursor: pointer;
    font-family: Livvic-SemiBold;
  }

  .nav-bar {
    margin-right: 600px;
    display: flex;
    gap: 10px;
    cursor: pointer;
    margin-top: 15px;
  }
  .contact-us {
    margin-top: 15px;

    button {
      border-radius: 25px;
      border: 1px white solid;
      color: white;
      background-color: $clr2;
      cursor: pointer;
      font-family: Livvic-Bold;
      padding: 10px;
    }
  }
}
